import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";
import Layout from "/src/layouts/main/layout/layout.js";
import Seo from "/src/components/seo"

import "/src/assets/scss/templates/thoughts.scss"

const ThoughtsTemplate = ({ data }) => {
  const  thoughts = data.markdownRemark
  /* Usar imagen en el top con parallax */
  const { description, title, frontimage, date } = thoughts.frontmatter
  const { slug } = thoughts.fields
  const content = thoughts.html
  
  return (
    <Layout>
      <div className="thoughts-content">
        <Seo />
        <h1>{title}</h1>
        { frontimage ? <GatsbyImage image={frontimage.childImageSharp?.gatsbyImageData} alt="" className="thoughts-item__img" /> : '' }
        <section className='thoughts-body' 
            dangerouslySetInnerHTML={{ __html: content }}
        ></section>
      </div>
    </Layout>
  )
  
}

export default ThoughtsTemplate

export const query = graphql`query ThoughtsBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(
        id: { eq: $id }
      ) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        frontimage {
            childImageSharp {
                gatsbyImageData(width: 960, height: 300, layout: CONSTRAINED)
            }
          }
        description
      }
      fields {
        slug
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`